import axios from "axios";
const baseUrl = "http://kosa1.iptime.org:50219/";
function getGrades() {
  return axios.get(baseUrl + "info/grade");
}

function getGrade(totalprice) {
  return axios.get(baseUrl + "info/getGrade?p=" + totalprice);
}

export default {
  getGrades,
  getGrade,
};
