<template>
  <v-container class="pa-0">
    <div class="pa-3">
      <v-row>
        <v-col class="text-h5 font-weight-bold mt-3">회원혜택</v-col>
      </v-row>
      <div class="pa-3 mt-5">
        <v-row class="text-h5 mb-1"> {{ $store.getters.getMemberId }}님의 회원등급은</v-row>
        <v-row class="text-h5">
          <span class="text-decoration-underline main-txt">{{ $store.getters.getGname }}</span>
          <span>입니다.</span>
        </v-row>
      </div>
    </div>

    <div class="pt-3 pb-2">
      <span class="text-h6 font-weight-bold mt-3 pb-0 pl-3"> 회원 등급별 혜택 </span>
    </div>

    <v-divider />

    <div v-for="(grade, idx) in grades" :key="grade.name" class="pa-0">
      <v-card flat class="pa-3">
        <v-card-actions>
          <v-container>
            <v-row>
              <span class="main-txt font-weight-bold" text> {{ grade.gname }} </span>
            </v-row>
            <v-row class="pt-2 font-weight-bold" v-if="idx < grades.length - 1">
              연간 {{ chageWon(grade.min) }}원 이상 ~ {{ chageWon(grade.max) }}원 미만
            </v-row>
            <v-row class="pt-2 font-weight-bold" v-if="idx == grades.length - 1"> 연간 {{ chageWon(grade.min) }}원 초과</v-row>
          </v-container>
          <v-spacer></v-spacer>

          <v-btn icon @click="$set(show, idx, !show[idx])">
            <v-icon v-if="show[idx]">mdi-chevron-up</v-icon>
            <v-icon v-if="!show[idx]">mdi-chevron-down</v-icon>
          </v-btn>
        </v-card-actions>

        <v-expand-transition>
          <div v-show="show[idx]">
            <v-card-text class="brown lighten-5 font-weight-bold"> 구매 금액의 {{ grade.benefit }}% 적립 </v-card-text>
          </div>
        </v-expand-transition>
      </v-card>
      <v-divider />
    </div>
  </v-container>
</template>

<script>
import GradeApi from "@/apis/grade";
export default {
  name: "Grade",
  data: () => ({
    gname: "Lv1",
    grades: [],
    show: [],
  }),
  methods: {
    chageWon(val) {
      if (val >= 10000000) {
        return "";
      } else if (val > 999999 && val < 10000000) {
        return val / 1000000 + "백만";
      } else if (val > 0 && val < 1000000) {
        return val / 10000 + "만";
      } else {
        return 0;
      }
    },
    async getGradeInfo() {
      try {
        let promise = await GradeApi.getGrades();
        this.grades = promise.data.list;
      } catch (error) {
        console.log(error);
      }
    },
  },
  created() {
    this.getGradeInfo();
  },
};
</script>

<style scoped>
.main-txt {
  color: #c2935f;
}
</style>
